html,body, #root{
    position: relative;
    width:100%;
    height:100%;
    margin:0;
}


#root{
    background:url('./1212.jpg') no-repeat;
    background-size: cover;
}

p{
    margin:0;
    line-height:1.5;
}

#root > div{
    width: 45vw;
    height:100vh;
    background:#000;
    position: relative;
}
#root > div > div{
    text-align: center;
    width:100%;
    transform: translate(-50%,-50%);
    position: absolute;
    top:50%;
    left:50%;
}
#root > div > div > button{
    display:block;
    margin:0 auto;
    width:240px;
    height:50px;
    margin-bottom:10px;
    font-size:16px;
    background:none;
    border:1px solid #fff;
    color:#fff;
}

#root > div > div > button.bottom{
    background:#fff;
    color:#000;
}

label, input{
    color:#fff;
    line-height:50px;
}
input{
    margin-bottom:40px;
    background:none;
    border:none;
    border-bottom:1px solid #fff;
    width:200px;
    text-align:center;
    color:#fff;
    outline:none;
    font-size:16px;

}
label{
    display:block;
}